const baseUrl = "https://r4xp4nhmja.execute-api.eu-west-2.amazonaws.com/default";

const DoLogs = true

function Log(...args) {
    if (DoLogs) {
        console.log(...args)
    }
}

const Fetch = function(url, init) {
    let headers

    if (init) {
        if (init.headers) {
            headers = init.headers
        } else {
            headers = {}
        }
    } else {
        headers = {}
    }

    Log(init)
    Log(headers)

    return fetch(`${baseUrl}${url}`, { ...init, headers })
}

const APIRequest = async function(method, id, params) {
    Log(id)
    Log(method)
    Log(params)

    const http_response = await Fetch("", {
        method: "POST",
        headers: {
            "Content-Type": "Application/json"
        },
        body: JSON.stringify({ method, id, params })
    })

    if (!http_response.ok) {
        throw Error("APIRequest: Couldn't get '" + method + "' response")
    }

    const json_response = await http_response.json()

    Log("Response:", json_response)

    const { result, status } = json_response

    if (status !== "ok") {
        throw Error("APIRequest: '" + method + "' request failed")
    }

    return result
}

const APIClient = (function() {
    return {
        NewCharacter: function(id, desc) {
            let params = {desc: desc}
            return APIRequest("new_character", id, params)
        },

        ChangeMood: function(id, new_mood) {
            let params = {new_mood: new_mood}
            return APIRequest("change_mood", id, params)
        },

        TriggerEvent: function(id, event_desc, attitude) {
            let params = {event_desc: event_desc, attitude: attitude}
            return APIRequest("trigger_event", id, params)
        },

        GetReplyFromCharacter: function(id, user_message, user_name) {
            let params = {user_message: user_message, user_name: user_name}
            return APIRequest("request_reply", id, params)
        },

        CreateUserSession: function() {
            let params = {}
            return APIRequest("create_user_session", params)
        },

        
    }
})()

export default APIClient